import HomeDashboard from "@/app/components/HomeDashboard";
import { prisma } from "@/app/lib/prisma";
import { getSession } from "@/app/lib/session";

export const runtime = "nodejs";

export default async function StatsPage() {
  const session = await getSession();
  const userId = session ? Number(session.sub) : null;

  const [cardPacks, decks] = await Promise.all([
    prisma.cardPack.findMany({ orderBy: { releaseDate: "desc" } }),
    prisma.deck.findMany({
      orderBy: { id: "desc" },
      include: { cardPack: true },
    }),
  ]);
  const matchups = await prisma.matchup.findMany({
    orderBy: { id: "desc" },
    include: {
      deck1: { include: { cardPack: true } },
      deck2: { include: { cardPack: true } },
      user: true,
    },
  });
  const records = await prisma.matchRecord.findMany({
    where: Number.isInteger(userId) ? { userId: userId as number } : undefined,
    orderBy: { id: "desc" },
    include: {
      deck: { include: { cardPack: true } },
      opponentDeck: { include: { cardPack: true } },
      user: true,
    },
  });

  return (
    <HomeDashboard
      decks={decks}
      cardPacks={cardPacks.map((pack) => ({
        ...pack,
        releaseDate: pack.releaseDate.toISOString(),
      }))}
      matchups={matchups}
      records={records}
    />
  );
}
